.footer {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
  hr {
    border: 0;
    border-top: 1px solid #ccc;
  }
}

.social-links {
  .social-link {
    margin: 0px 10px 10px 10px;
    img {
      width: 32px;
      height: 32px;
    }
  }
  .linkedin {
    color: blue;
  }
  .kaggle {
    color: skyblue;
  }
}

